import { notFound } from "next/navigation"
import Link from "next/link"
import { Calendar } from "lucide-react"
import { sanityClient } from "@/lib/sanity"
import BlogHeader from "@/components/BlogHeader"
import Footer from "@/components/footer"
import BlogShareSidebar from "@/components/BlogShareSidebar"
import { RelatedPosts } from "@/components/RelatedPosts"
import { PortableText } from "@portabletext/react"
import { portableTextComponents } from "@/lib/portableTextComponents"

/* ----------------------------------------
   SEO METADATA
---------------------------------------- */
export async function generateMetadata({
  params,
}: {
  params: Promise<{ slug: string }>
}) {
  const { slug } = await params

  try {
    const post = await sanityClient.fetch(
      `*[_type=="post" && slug.current==$slug][0]{
        title,
        excerpt
      }`,
      { slug }
    )

    if (!post) {
      return {
        title: "Blog | Cloud101",
        description: "Salesforce and Cloud learning articles by Cloud101.",
      }
    }

    return {
      title: `${post.title} | Cloud101`,
      description: post.excerpt?.slice(0, 155),
      alternates: {
        canonical: `https://cloud101.in/blog/${slug}`,
      },
    }
  } catch {
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101.",
    }
  }
}

/* ----------------------------------------
   PAGE
---------------------------------------- */
export default async function BlogPostPage({
  params,
}: {
  params: Promise<{ slug: string }>
}) {
  const { slug } = await params

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      _id,
      title,
      excerpt,
      publishedAt,
      mainImage{ asset->{url} },
      categories[]->{_id,title},
      author->{ name },
      content
    }`,
    { slug }
  )

  if (!post) {
    notFound()
  }

  return (
    <>
      <BlogHeader />
      <BlogShareSidebar />

      <main className="max-w-4xl mx-auto px-5 py-16 pt-20">
        {/* Back */}
        <Link
          href="/blog"
          className="inline-flex items-center gap-2 text-sm text-blue-600 hover:underline mb-6"
        >
          ‚Üê Back to Blog
        </Link>

        {/* Categories */}
        <div className="flex flex-wrap gap-2 mb-4">
          {post.categories?.map((cat: any) => (
            <span
              key={cat._id}
              className="text-xs font-medium bg-blue-50 text-blue-600 px-3 py-1 rounded-full"
            >
              {cat.title}
            </span>
          ))}
        </div>

        {/* Title */}
        <h1 className="text-4xl md:text-5xl font-bold tracking-tight text-gray-900 mb-6">
          {post.title}
        </h1>

        {/* Meta */}
        <div className="flex flex-wrap items-center gap-6 text-sm text-gray-500 mb-10">
          <span className="flex items-center gap-2">
            <Calendar size={16} />
            {new Date(post.publishedAt).toLocaleDateString("en-IN", {
              day: "numeric",
              month: "long",
              year: "numeric",
            })}
          </span>

          {post.author?.name && (
            <span className="font-medium text-gray-700">
              {post.author.name}
            </span>
          )}
        </div>

        {/* Excerpt */}
        {post.excerpt && (
          <p className="text-lg font-medium text-gray-700 leading-relaxed mb-10 border-l-4 border-blue-600 pl-4">
            {post.excerpt}
          </p>
        )}

        {/* Featured Image */}
        {post.mainImage?.asset?.url && (
          <div className="relative w-full h-[420px] mb-14 rounded-3xl overflow-hidden">
            <img
              src={post.mainImage.asset.url}
              alt={post.title}
              className="absolute inset-0 w-full h-full object-cover"
            />
          </div>
        )}

        {/* Content */}
        <article
          className="
    prose 
    prose-lg 
    prose-slate 
    max-w-none

    prose-headings:font-bold
    prose-headings:tracking-tight
    prose-headings:text-gray-900

    prose-p:text-gray-700
    prose-p:leading-8

    prose-a:text-blue-600
    hover:prose-a:underline

    prose-strong:text-gray-900
  "
        >
          <PortableText
            value={post.content}
            components={portableTextComponents}
          />
        </article>


        {/* Related Posts */}
        <RelatedPosts
          currentPostId={post._id}
          categories={post.categories}
        />
      </main>

      {/* FULL WIDTH CTA */}
      <section className="bg-gradient-to-r from-blue-50 to-blue-100 py-12 mt-16">
        <div className="max-w-7xl mx-auto px-4 text-center">
          <h2 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-4">
            Ready to Master Salesforce?
          </h2>
          <p className="text-gray-600 mb-6 max-w-2xl mx-auto">
            Join our next batch and get hands-on training from industry experts.
          </p>
          <a
            href="/#contact"
            className="inline-block px-8 py-3 bg-[#0B66FF] text-white rounded-full font-semibold hover:bg-[#0546C8] transition-colors"
          >
            Enroll Now
          </a>
        </div>
      </section>

      <Footer />
    </>
  )
}