import Link from "next/link"
import { sanityClient } from "@/lib/sanity"

interface RelatedPostsProps {
  currentPostId: string
  categories: { _id: string; title: string }[]
}

export async function RelatedPosts({
  currentPostId,
  categories,
}: RelatedPostsProps) {
  if (!categories?.length) return null

  const categoryIds = categories.map((c) => c._id)

  const relatedPosts = await sanityClient.fetch(
    `*[_type=="post" &&
      references($categoryIds) &&
      _id != $currentPostId][0...3]{
        _id,
        title,
        "slug": slug.current,
        excerpt,
        mainImage{ asset->{url} }
      }`,
    { categoryIds, currentPostId }
  )

  if (!relatedPosts?.length) return null

  return (
    <section className="mt-24">
      <h2 className="text-2xl font-bold mb-8">Related articles</h2>

      <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
        {relatedPosts.map((post: any) => (
          <Link
            key={post._id}
            href={`/blog/${post.slug}`}
            className="group bg-white border rounded-2xl overflow-hidden hover:shadow-lg transition-all flex flex-col"
          >
            {post.mainImage?.asset?.url && (
              <img
                src={post.mainImage.asset.url}
                alt={post.title}
                className="h-48 w-full object-cover group-hover:scale-105 transition-transform"
              />
            )}

            <div className="p-6 flex flex-col flex-1">
              <h3 className="font-semibold text-lg mb-2 line-clamp-2">
                {post.title}
              </h3>

              {post.excerpt && (
                <p className="text-sm text-gray-600 line-clamp-3">
                  {post.excerpt}
                </p>
              )}
            </div>
          </Link>
        ))}
      </div>
    </section>
  )
}