import Image from "next/image"
import type { PortableTextComponents } from "@portabletext/react"

export const portableTextComponents: PortableTextComponents = {
  /* ----------------------------------------
     BLOCK TYPES
  ---------------------------------------- */
  types: {
    image: ({ value }) => {
      if (!value?.asset?._ref) return null

      const imageUrl = value.asset.url || value.asset._ref

      return (
        <div className="my-10">
          <img
            src={imageUrl}
            alt={value.alt || "Blog image"}
            className="w-full rounded-xl"
          />
          {value.caption && (
            <p className="text-center text-sm text-gray-500 mt-2">
              {value.caption}
            </p>
          )}
        </div>
      )
    },

    code: ({ value }) => {
      return (
        <pre className="bg-gray-900 text-gray-100 rounded-xl p-5 overflow-x-auto my-8">
          <code className="text-sm leading-relaxed">
            {value.code}
          </code>
        </pre>
      )
    },
  },

  /* ----------------------------------------
     MARKS (INLINE)
  ---------------------------------------- */
  marks: {
    link: ({ value, children }) => {
      const target = value?.blank ? "_blank" : undefined
      return (
        <a
          href={value?.href}
          target={target}
          rel={target === "_blank" ? "noopener noreferrer" : undefined}
          className="text-blue-600 underline"
        >
          {children}
        </a>
      )
    },
  },

  /* ----------------------------------------
     LISTS
  ---------------------------------------- */
  list: {
    bullet: ({ children }) => (
      <ul className="list-disc pl-6 my-6 space-y-2">{children}</ul>
    ),
    number: ({ children }) => (
      <ol className="list-decimal pl-6 my-6 space-y-2">{children}</ol>
    ),
  },

  listItem: {
    bullet: ({ children }) => <li>{children}</li>,
    number: ({ children }) => <li>{children}</li>,
  },
}
